.header {
    position: sticky;
    top: 0;
    z-index: 50;
    background: rgba(10, 10, 15, 0.8);
    backdrop-filter: blur(20px);
    -webkit-backdrop-filter: blur(20px);
    border-bottom: 1px solid var(--border-subtle);
}

.container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: var(--space-6);
    max-width: 1400px;
    margin: 0 auto;
    padding: var(--space-3) var(--space-6);
}

/* Logo */
.logo {
    display: flex;
    align-items: center;
    gap: var(--space-2);
    text-decoration: none;
}

.logoIcon {
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: linear-gradient(135deg, var(--primary-600), var(--accent-pink));
    border-radius: var(--radius-lg);
    color: white;
}

.logoText {
    font-size: 1.5rem;
    font-weight: 800;
    font-family: var(--font-display);
    background: var(--gradient-primary);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}

/* Desktop Nav */
.desktopNav {
    display: flex;
    align-items: center;
    gap: var(--space-1);
}

.navLink {
    display: flex;
    align-items: center;
    gap: var(--space-2);
    padding: var(--space-2) var(--space-4);
    font-size: 0.9375rem;
    font-weight: 500;
    color: var(--text-secondary);
    border-radius: var(--radius-lg);
    transition: all var(--transition-fast);
    text-decoration: none;
}

.navLink:hover {
    color: var(--text-primary);
    background: rgba(255, 255, 255, 0.05);
}

.navLink svg {
    opacity: 0.7;
}

/* Right Section */
.rightSection {
    display: flex;
    align-items: center;
    gap: var(--space-4);
}

.streakWrapper {
    display: block;
}

.xpWrapper {
    display: block;
    min-width: 180px;
}

.profileButton {
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--bg-card);
    border: 1px solid var(--border-subtle);
    border-radius: 50%;
    color: var(--text-secondary);
    cursor: pointer;
    transition: all var(--transition-fast);
}

.profileButton:hover {
    border-color: var(--border-glow);
    color: var(--text-primary);
}

/* Mobile Menu Toggle */
.mobileMenuToggle {
    display: none;
    width: 40px;
    height: 40px;
    align-items: center;
    justify-content: center;
    background: transparent;
    border: none;
    color: var(--text-primary);
    cursor: pointer;
}

/* Mobile Menu */
.mobileMenu {
    overflow: hidden;
    border-top: 1px solid var(--border-subtle);
}

.mobileNav {
    display: flex;
    flex-direction: column;
    padding: var(--space-4);
    gap: var(--space-1);
}

.mobileNavLink {
    display: flex;
    align-items: center;
    gap: var(--space-3);
    padding: var(--space-3) var(--space-4);
    font-size: 1rem;
    font-weight: 500;
    color: var(--text-secondary);
    border-radius: var(--radius-lg);
    text-decoration: none;
    transition: all var(--transition-fast);
}

.mobileNavLink:hover {
    background: rgba(255, 255, 255, 0.05);
    color: var(--text-primary);
}

.mobileGameStats {
    display: flex;
    flex-direction: column;
    gap: var(--space-4);
    padding: var(--space-4);
    border-top: 1px solid var(--border-subtle);
}

.mobileActions {
    padding: var(--space-4);
    border-top: 1px solid var(--border-subtle);
}

/* Responsive */
@media (max-width: 1024px) {
    .xpWrapper {
        display: none;
    }
}

@media (max-width: 768px) {
    .container {
        padding: var(--space-3) var(--space-4);
    }

    .desktopNav {
        display: none;
    }

    .streakWrapper {
        display: none;
    }

    .xpWrapper {
        display: none;
    }

    .mobileMenuToggle {
        display: flex;
    }

    .rightSection>button:not(.mobileMenuToggle) {
        display: none;
    }
}