.card {
    position: relative;
    display: flex;
    flex-direction: column;
    background: var(--bg-card);
    border: 1px solid var(--border-subtle);
    border-radius: var(--radius-xl);
    overflow: hidden;
    cursor: pointer;
    transition: all var(--transition-normal);
}

.card:hover {
    border-color: var(--border-glow);
    box-shadow: var(--shadow-lg), var(--shadow-glow);
}

.featured {
    border-color: rgba(139, 92, 246, 0.3);
}

/* Badges */
.badges {
    position: absolute;
    top: var(--space-3);
    left: var(--space-3);
    display: flex;
    gap: var(--space-2);
    z-index: 2;
}

/* Actions */
.actions {
    position: absolute;
    top: var(--space-3);
    right: var(--space-3);
    display: flex;
    gap: var(--space-2);
    z-index: 2;
    opacity: 0;
    transition: opacity var(--transition-fast);
}

.card:hover .actions {
    opacity: 1;
}

.actionButton {
    width: 36px;
    height: 36px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(0, 0, 0, 0.6);
    backdrop-filter: blur(8px);
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 50%;
    color: var(--text-secondary);
    cursor: pointer;
    transition: all var(--transition-fast);
}

.actionButton:hover {
    background: rgba(0, 0, 0, 0.8);
    color: var(--text-primary);
}

.actionButton.saved {
    color: var(--accent-rose);
}

/* Thumbnail */
.thumbnail {
    position: relative;
    width: 100%;
    height: 140px;
    overflow: hidden;
}

.thumbnail img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform var(--transition-slow);
}

.card:hover .thumbnail img {
    transform: scale(1.05);
}

.thumbnailOverlay {
    position: absolute;
    inset: 0;
    background: linear-gradient(180deg,
            transparent 0%,
            rgba(10, 10, 15, 0.8) 100%);
}

/* Content */
.content {
    display: flex;
    flex-direction: column;
    gap: var(--space-3);
    padding: var(--space-5);
}

/* Category */
.category {
    display: flex;
    align-items: center;
    gap: var(--space-2);
    font-size: 0.75rem;
    font-weight: 600;
    color: var(--text-muted);
    text-transform: uppercase;
    letter-spacing: 0.05em;
}

.categoryDot {
    width: 8px;
    height: 8px;
    border-radius: 50%;
}

/* Title */
.title {
    font-size: 1.0625rem;
    font-weight: 700;
    font-family: var(--font-display);
    color: var(--text-primary);
    margin: 0;
    line-height: 1.4;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
}

/* Tags */
.tags {
    display: flex;
    flex-wrap: wrap;
    gap: var(--space-2);
}

.tag {
    padding: var(--space-1) var(--space-2);
    font-size: 0.6875rem;
    font-weight: 500;
    color: var(--text-muted);
    background: rgba(255, 255, 255, 0.05);
    border-radius: var(--radius-sm);
}

/* Meta */
.meta {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: var(--space-3);
}

.metaItem {
    display: flex;
    align-items: center;
    gap: var(--space-2);
    color: var(--text-muted);
}

.difficulty {
    font-size: 0.75rem;
    font-weight: 600;
}

/* Footer */
.footer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: var(--space-3);
    border-top: 1px solid var(--border-subtle);
}

.applicants,
.rating {
    display: flex;
    align-items: center;
    gap: var(--space-1);
    font-size: 0.8125rem;
    color: var(--text-muted);
}

.rating {
    color: var(--accent-amber);
}

/* Featured Glow */
.featuredGlow {
    position: absolute;
    inset: -1px;
    background: linear-gradient(135deg,
            rgba(139, 92, 246, 0.2) 0%,
            transparent 50%,
            rgba(236, 72, 153, 0.1) 100%);
    border-radius: var(--radius-xl);
    z-index: -1;
    opacity: 0;
    transition: opacity var(--transition-normal);
}

.card:hover .featuredGlow {
    opacity: 1;
}

/* Compact Card */
.compactCard {
    display: flex;
    align-items: center;
    gap: var(--space-3);
    padding: var(--space-3) var(--space-4);
    background: var(--bg-card);
    border: 1px solid var(--border-subtle);
    border-radius: var(--radius-lg);
    cursor: pointer;
    transition: all var(--transition-fast);
}

.compactCard:hover {
    background: var(--bg-card-hover);
    border-color: var(--border-glow);
}

.compactIndicator {
    width: 4px;
    height: 32px;
    border-radius: var(--radius-full);
    flex-shrink: 0;
}

.compactContent {
    flex: 1;
    min-width: 0;
}

.compactTitle {
    font-size: 0.9375rem;
    font-weight: 600;
    color: var(--text-primary);
    margin: 0 0 var(--space-1) 0;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.compactMeta {
    display: flex;
    align-items: center;
    gap: var(--space-3);
}

.compactCategory {
    font-size: 0.75rem;
    color: var(--text-muted);
}